import React, {Component} from 'react';

class Header extends Component {

    state = {
        showHome: window.innerWidth > 1320,
        showLogo: window.innerWidth <= 990,
        isBurgerOpen: false,
        isSearchOpen: false,
        isMoneyOpen: false
    }

    componentDidMount() {
        window.addEventListener("resize", this.resize.bind(this));
        this.resize();
    }

    resize() {
        this.setState({showHome: window.innerWidth > 1320, showLogo: window.innerWidth <= 990});
    }

    render() {
        const label = this.state.showHome && <a id='label' href='index.html'>Control.net.ua</a>
        const textArea = (this.state.showHome || this.state.isSearchOpen) && <input placeholder="Поиск"/>;
        const styleSearch = this.state.isSearchOpen ? '90%': 'inherit'
        const search = !this.state.isBurgerOpen && (
            <div id='search' style = {{width: styleSearch}}>
                <button id='srcBtn' type="submit" onClick={this.changeStatusSearch.bind(this)}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor"
                         className="bi bi-search" viewBox="0 0 16 16">
                        <path
                            d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z"/>
                    </svg>
                </button>
                {textArea}
                <div id='srcClose' style={{width: '40px', height: '40px', display: 'none'}}>
                    <button
                        style={{
                            outline: 'none',
                            border: '0',
                            background: 'transparent',
                            position: 'relative',
                            top: '4px',
                            left: '-2px'
                        }}>
                        <img src="../images/close.png" style={{width: '50%', height: '50%'}} alt="not_found"/>
                    </button>
                </div>
            </div>)
        const mainNav = this.state.showHome ? (
            <nav id="nav" className="navbar navbar-expand-lg navbar-dark">
                <button onClick={this.openMenu} id='burger' className="navbar-toggler" type="button"
                        data-toggle="collapse"
                        data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent"
                        aria-expanded="false"
                        aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"/>
                </button>
                {label}
                <div className='collapse navbar-collapse' id='navbarSupportedContent'>
                    <ul className="navbar-nav mr-auto">
                        <li className="nav-item dropdown">
                            <a href='#' className="nav-link dropdown-toggle" id="navbarDropdown" role="button"
                               data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                real money
                            </a>
                            <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                                <a className="dropdown-item" href="#">Action</a>
                                <a className="dropdown-item" href="#">Another action</a>
                                <a className="dropdown-item" href="#">Something else here</a>
                            </div>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#">free slots</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#">slot features</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#">casino soft</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#">online</a>
                        </li>
                    </ul>
                </div>
                <div id='search'>
                    <div id='srcClose' style={{width: '40px', height: '40px', display: 'none'}}>
                        <button
                            style={{
                                outline: 'none',
                                border: '0',
                                background: 'transparent',
                                position: 'relative',
                                top: '4px',
                                left: '-2px'
                            }}>
                            <img src="../images/close.png" style={{width: '50%', height: '50%'}}
                                 alt="not_found"></img></button>
                    </div>
                    {textArea}
                    <button id='srcBtn' type="submit">
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor"
                             className="bi bi-search" viewBox="0 0 16 16">
                            <path
                                d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z"/>
                        </svg>
                    </button>
                </div>
            </nav>
        ) : (
            <nav id="nav" className="navbar navbar-expand-lg navbar-dark">
                {search}
                {this.state.showLogo && !this.state.isBurgerOpen && !this.state.isSearchOpen && <h1 id='Logo'>LOGO</h1>}
                {!this.state.isSearchOpen &&
                <button id='burger' onClick={this.openMenu.bind(this)} className="navbar-toggler" type="button"
                        data-toggle="collapse"
                        data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent"
                        aria-expanded="false"
                        aria-label="Toggle navigation">
                    {this.state.isBurgerOpen ? <span>X</span> : <span className="navbar-toggler-icon"/>}
                </button>
                }
                {label}
                <div className='collapse navbar-collapse' id='navbarSupportedContent'>
                    <ul className="navbar-nav mr-auto" style = {{justifyContent:'space-around!important'}}>
                        <li className="nav-item dropdown">
                            <a href='#' className="nav-link dropdown-toggle" id="navbarDropdown" role="button"
                               data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                real money
                            </a>
                            <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                                <a className="dropdown-item" href="#">Action</a>
                                <a className="dropdown-item" href="#">Another action</a>
                                <a className="dropdown-item" href="#">Something else here</a>
                            </div>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#">free slots</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#">slot features</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#">casino soft</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#">online</a>
                        </li>
                    </ul>
                </div>
            </nav>
        )
        return (
            <header>
                {mainNav}
            </header>
        )
    }

    changeStatusSearch(){
        this.setState( {
            isSearchOpen: !this.state.isSearchOpen
        });
    }

    openMenu() {
        this.setState({
            isBurgerOpen: !this.state.isBurgerOpen
        });
    }
}

export default Header